(function() {
  'use strict';

  angular
    .module('accountBook')
    .controller('NavbarController', NavbarController)
    .directive('acmeNavbar', acmeNavbar);

  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      require: '^acmeNavbar',
      scope: {
          creationDate: '='
      },
      controller: 'NavbarController',
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;
  }

  function NavbarController(IndexFactory, localStorageService, $location, $scope, toastr, $translate) {
    var vm = this;

    $scope.$watch(function() {
      return $location.path();
    }, function() {
      vm.login_status = IndexFactory.getLoginStatus();
      IndexFactory.getCurrentUser().then(function(res) {
        vm.current_user = res;
      })
    });
    vm.logout = function() {
      localStorageService.remove('access_token');
      vm.login_status = IndexFactory.getLoginStatus();
      toastr.success($translate.instant('MESSAGES.LOGOUT'));
      $location.path('/');
    };
  }

})();

(function() {
  'use strict';

  angular
    .module('accountBook')
    .controller('MenuController', MenuController)
    .directive('menuDirective', menuDirective);

  function menuDirective() {
    var directive = {
      templateUrl: 'app/components/menu/menu.html',
      controller: 'MenuController',
      controllerAs: 'menu',
      bindToController: true
    };
    return directive;
  }

  function MenuController() {
    var vm = this;
    vm.menu_image = 'assets/images/pig_footprints.gif';
    vm.isSelected = function(index) {
      return vm.selected == index;
    };

    // Event
    vm.mouseEnter = function(index) {
      vm.selected = index;
    };
    vm.mouseLeave = function() {
      vm.selected = undefined;
    };
  }
  
})();

(function() {
  'use strict';

  angular
    .module('accountBook')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('accountBook')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

