(function() {
  'use strict';

  angular
    .module('accountBook')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr, $location, $translate) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1447644390420;
    vm.showToastr = showToastr;
    if ($location.search()['registed'] == 'ok') {
      $location.url('/login');
      toastr.success($translate.instant('MESSAGES.REGISTED'));
    }
    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

(function() {
  'use strict';

    angular
      .module('accountBook')
      .controller('AdminController', AdminController);

    function AdminController(IndexFactory) {
      var vm = this;

      vm.offset = 0;
      vm.clickPaginate = function(offset) {
        vm.offset = offset;
        IndexFactory.getUsers(vm.offset).then(function(res) {
          vm.users = res.users;
          vm.total_count = res.total_count;
          var total_array = [];
          for (var i=0; i<=vm.total_count; i++) {
            total_array.push(i);
          }
          vm.offset_numbers = total_array.filter(function(x) { return x % 20 === 0; });
        });
      };

      IndexFactory.getUsers(vm.offset).then(function(res) {
        vm.users = res.users;
        vm.total_count = res.total_count;
          var total_array = [];
          for (var i=0; i<=vm.total_count; i++) {
            total_array.push(i);
          }
          vm.offset_numbers = total_array.filter(function(x) { return x % 20 === 0; });
      });
    }

})();

