(function() {
  'use strict';

  angular
    .module('accountBook')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('admin', {
        url: '/admin',
        templateUrl: 'app/admin/admin.html',
        controller: 'AdminController',
        controllerAs: 'admin'
      })
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('login', {
        url: '/login',
        templateUrl: 'app/account/login.html',
        controller: 'LoginController',
        controllerAs: 'login'
      })
      .state('sign_up', {
        url: '/sign_up',
        templateUrl: 'app/account/sign_up.html',
        controller: 'SignUpController',
        controllerAs: 'sign_up'
      })
      .state('reset_password', {
        url: '/reset_password',
        templateUrl: 'app/account/reset_password.html',
        controller: 'ResetPasswordController',
        controllerAs: 'reset_password'
      })
      .state('edit_password', {
        url: '/edit_password',
        templateUrl: 'app/account/edit_password.html',
        controller: 'EditPasswordController',
        controllerAs: 'edit_password'
      })
      .state('resend_email', {
        url: '/resend_email',
        templateUrl: 'app/account/resend_email.html',
        controller: 'ResendEmailController',
        controllerAs: 'resend_email'
      })
      .state('menu', {
        url: '/menu',
        templateUrl: 'app/components/menu/menu.html',
        controller: 'MenuController',
        controllerAs: 'menu'
      })
      .state('mypage', {
        url: '/mypage',
        templateUrl: 'app/user/mypage.html',
        controller: 'MypageController',
        controllerAs: 'mypage'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

(function() {
  'use strict';

    angular
      .module('accountBook')
      .factory('IndexFactory', IndexFactory);

    function IndexFactory($http, $location, $q, localStorageService, $translate, toastr) {
      var host = ($location.host() == 'localhost') ? 'http://localhost:3001/' : '';

      return ({
        getLoginStatus: function() {
          var token = localStorageService.get('access_token');
          return typeof(token) != "undefined" && token != null;
        },
        getCurrentUser: function() {
          var defer = $q.defer();
          var token = localStorageService.get('access_token');
          if (this.getLoginStatus()) {
            var login_headers = { headers: { Authorization: "Token token=" + token }};
            $http.get(host + 'user/', login_headers)
              .success(function(data) {
                defer.resolve(data);
              })
              .error(function(data) {
                defer.reject(data);
              });
          }
          return defer.promise;
        },
        postEmailUserRegistrations: function(params) { 
          var defer = $q.defer();
          $http.post(host + 'email_user/registrations', params)
            .success(function(data) {
              defer.resolve(data);
              toastr.success($translate.instant('MESSAGES.SEND_MAIL'));
              $location.path('/');
            })
            .error(function(data) {
              if (typeof data != 'undefined') {
                defer.reject(data);
              }
            });
          return defer.promise;
        },
        postSession: function(params) {
          var defer = $q.defer();
          $http.post(host + 'session', params)
            .success(function(data) {
              defer.resolve(data);
              localStorageService.set('access_token', data.access_token);
              toastr.success($translate.instant('MESSAGES.LOGIN'));
              $location.path('/');
            })
            .error(function(data) {
              defer.reject(data);
            });
          return defer.promise;
        },
        postResendEmail: function(params) {
          var defer = $q.defer();
          $http.patch(host + 'email_user/registrations/recreate', params)
            .success(function(data) {
              defer.resolve(data);
              toastr.success($translate.instant('MESSAGES.SEND_MAIL'));
              $location.path('/');
            })
            .error(function(data) {
              defer.reject(data);
            });
          return defer.promise;
        },
        postResetPassword: function(params) {
          var defer = $q.defer();
          $http.post(host + 'email_user/passwords/send_mail', params)
            .success(function(data) {
              defer.resolve(data);
              toastr.success($translate.instant('MESSAGES.SEND_MAIL'));
              $location.path('/');
            })
            .error(function(data) {
              defer.reject(data);
            });
          return defer.promise;
        },
        getUsers: function(offset) {
          var defer = $q.defer();
          var token = localStorageService.get('access_token');
          if (this.getLoginStatus()) {
            var login_headers = { headers: { Authorization: "Token token=" + token }};
            $http.get(host + "admin/users?offset=" + offset, login_headers)
              .success(function(data) {
                defer.resolve(data);
              })
              .error(function(data) {
                defer.reject(data);
              });
          }
          return defer.promise;
        }
      });
    }
})();

